<template>
  <div>
    <!-- 帅选条件 -->
    <div class="formDiv">
      <el-form :inline="true" class="demo-form-inline">
        <!-- 查询条件 -->

        <el-form-item label="生产计划">
          <el-select v-model="listQuery.product_plan_code" placeholder="请选择">
            <el-option
              v-for="(item, index) in ProductPlanCodeNameList"
              :key="index + ' search'"
              :label="item.product_plan_name"
              :value="item.product_plan_code"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="包装编码">
          <el-input v-model="listQuery.pack_id"></el-input>
        </el-form-item>
        <el-form-item label="操作人员">
          <el-select
            v-model="listQuery.enter_user"
            placeholder="请选择"
            style="width: 100%"
          >
            <el-option
              v-for="(item, index) in PersonCodeNameList"
              :key="index + ' dialog'"
              :label="item.user_name"
              :value="item.user_code"
            ></el-option>
          </el-select>
        </el-form-item>
        <!-- 查询按钮 -->
        <el-form-item style="margin-left: 20px !important">
          <el-button type="primary" size="medium" @click="searchBtn"
            >查询</el-button
          >
          <el-button size="medium" @click="resetForm()">重置</el-button>
        </el-form-item>
      </el-form>
    </div>
    <!-- 表格 -->
    <el-card class="box-card mt20">
      <!-- <div slot="header" class="clearfix" style="line-height: 40px">
        <span>成品列表</span>
      </div> -->
      <div class="">
        <!-- <el-form :inline="true" class="demo-form-inline">
          <el-form-item>
            <el-button type="primary" size="medium" plain @click="exportBtn"
              ><i class="el-icon-plus"></i> 导出</el-button
            >
          </el-form-item>
        </el-form> -->
        <ELTable
          :tableCol="tableCol"
          :tableData="tableData"
          :listQuery="listQuery"
          :ispagination="ispagination"
          @currentChange="currentChange"
          @sizeChange="sizeChange"
        >
        </ELTable>
      </div>
    </el-card>

    <!-- 修改弹出层 -->
    <el-dialog
      :title="dialogTitle"
      width="1000px"
      :visible.sync="dialogFlag"
      :close-on-click-modal="false"
    >
      <el-form
        class=""
        style="padding: 0 60px 0 20px; max-height: 500px"
        ref="dialogFormRef"
        :model="dialogForm"
        :rules="dialogFormRules"
        label-width="100px"
      >
        <el-row>
          <el-col
            :span="12"
            v-for="(item, index) in dialogForm.response_datas"
            :key="index + ' onlyItem'"
          >
            <el-form-item
              :prop="'response_datas.' + index + '.finished_product_code'"
              :rules="[]"
              label="产品编码"
            >
              <el-row :gutter="10">
                <el-col :span="16">
                  <el-input
                    :ref="'finishedProductCode' + index"
                    v-model="item.finished_product_code"
                  ></el-input>
                </el-col>
                <el-col :span="6">
                  <el-button
                    size="small"
                    type="danger"
                    plain
                    @click="handleDelete(index, item)"
                    >删除</el-button
                  >
                </el-col>
              </el-row>
            </el-form-item>
          </el-col>
        </el-row>
      </el-form>

      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFlag = false">取 消</el-button>
        <el-button type="primary" @click="dialogSave">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import ELTable from "@/components/formTablePage/ElTable";
import { baseManagement } from "@/request/api";
import { mapState, mapActions } from "vuex";

export default {
  name: "UserList",
  components: {
    ELTable,
  },
  //   watch: {
  //     CurrentUserInfo: function (val, oldVal) {
  //       //当val变更之后在执行接下来的操作
  //       console.log(" val val val val val",val);
  //       console.log(" oldVal oldVal oldVal oldVal oldVal",oldVal);
  //     },
  //   },
  data() {
    return {
      // 筛选
      listQuery: {
        product_plan_code: "",
        enter_time: "",
        enter_user: "",
        page: 1,
        page_size: 10,
        total: 0,
      },
      listQueryBase: {
        product_plan_code: "",
        enter_time: "",
        enter_user: "",
        page: 1,
        page_size: 10,
        total: 0,
      },
      ispagination: true, // 分页是否显示
      // 新增包装弹出层
      dialogFlag: false,
      dialogForm: {
        response_datas: [],
      },
      dialogFormBase: {
        response_datas: [],
      },
      dialogFormRules: {
        user_id: [{ required: true, message: "请输入员工号", trigger: "blur" }],
        user_name: [
          { required: true, message: "请输入用户名", trigger: "blur" },
        ],
        user_password: [
          { required: true, message: "请输入密码", trigger: "blur" },
        ],
        status: [{ required: true, message: "请选择状态", trigger: "change" }],
      },
      // 表格列
      tableCol: [
        {
          label: "生产计划",
          field: "product_plan_name",
          align: "center",
        },
        {
          label: "包装编码",
          field: "pack_id",
          align: "center",
        },
        {
          label: "产品编码",
          field: "product_id",
          align: "center",
        },
        {
          label: "产品名称",
          field: "product_name",
          align: "center",
        },
        {
          label: "数量",
          field: "product_count",
          align: "center",
        },
        {
          label: "操作人员",
          width:"200px",
          field: "user_name",
          align: "center",
        },
        {
          label: "状态",
          width:"200px",
          field: "status",
          align: "center",
        },
        {
          label: "时间",
          field: "enter_time",
          align: "center",
        },
        {
          label: "操作",
          colType: "solt",
          align: "center",
          option: [
            {
              label: "修改",
              event: this.colEditBtn,
            },
            {
              label: "删除",
              event: this.colDeleteBtn,
            },
          ],
        },
      ],
      tableData: [],
    };
  },

  computed: {
    ...mapState([
      "PersonCodeNameList",
      "ProductPlanCodeNameList",
      "CurrentUserInfo",
    ]),
  },
  methods: {
    ...mapActions([
      "getLogisticPersonCodeName",
      "getLogisticProductPlanCodeName",
    ]),
    // 查询
    searchBtn() {
      this.listQuery.page = 1;
      this.getLogisticFinishedProductStorage(this.listQuery);
    },
    // 重置
    resetForm() {
      this.listQuery = JSON.parse(JSON.stringify(this.listQueryBase));
      this.getLogisticFinishedProductStorage(this.listQuery);
    },
    // 当前页面改变时
    currentChange(val) {
      this.listQuery.page = val;
      this.getLogisticFinishedProductStorage(this.listQuery);
    },
    // 每页条数发生改变时
    sizeChange(val) {
      this.listQuery.page = 1;
      this.listQuery.page_size = val;
      this.getLogisticFinishedProductStorage(this.listQuery);
    },
    // 成品列表 查询
    async getLogisticFinishedProductStorage(params) {
      baseManagement.getLogisticFinishedProductStorage(params).then((res) => {
        console.log("成品列表 查询", res);
        if (res.code == 0) {
          let tableData = res.data.data;
          tableData.forEach((tableDataItem) => {
            if (tableDataItem.response_datas) {
              tableDataItem.product_count = tableDataItem.response_datas.length;
            } else {
              tableDataItem.product_count = 0;
            }

            this.ProductPlanCodeNameList.forEach((CodeNameListItem) => {
              if (
                tableDataItem.product_plan_code ==
                CodeNameListItem.product_plan_code
              ) {
                tableDataItem.product_plan_name =
                  CodeNameListItem.product_plan_name;
              }
            });

            this.PersonCodeNameList.forEach((CodeNameListItem) => {
              if (tableDataItem.enter_user == CodeNameListItem.user_code) {
                tableDataItem.user_name = CodeNameListItem.user_name;
              }
            });
          });
          this.tableData = tableData;
          this.listQuery.total = res.data.total;
        } else {
          this.$message({
            message: res.message,
            type: "error",
          });
        }
      });
    },
    // 成品列表 新增
    getLogisticFromDataModel(params) {
      baseManagement.postLogisticPersonDeal(params).then((res) => {
        console.log("成品列表 新增", res);
        if (res.code == 0) {
          this.dialogFlag = false;
          this.getLogisticFinishedProductStorage(this.listQuery);
          this.$message({
            message: res.message,
            type: "success",
          });
        } else {
          this.$message({
            message: res.message,
            type: "error",
          });
        }
      });
    },


    // 成品列表 修改
    colEditBtn(rowData) {
      console.log(rowData);
      this.dialogTitle = "修改包装";
      this.dialogFlag = true;
      this.$nextTick(() => {
        this.$refs.dialogFormRef.clearValidate();
      });
      this.dialogForm = {
        enter_time: rowData.enter_time,
        enter_user: rowData.enter_user,
        pack_id: rowData.pack_id,
        product_id: rowData.product_id,
        product_name: rowData.product_name,
        status: rowData.status,
        response_datas: rowData.response_datas,
      };
    },

    // 成品列表 修改
    getLogisticPutFinishedProductStorage(params) {
      baseManagement
        .getLogisticPutFinishedProductStorage(params)
        .then((res) => {
          console.log("成品列表 修改", res);
          if (res.code == 0) {
            this.dialogFlag = false;
            this.getLogisticFinishedProductStorage(this.listQuery);
            this.$message({
              message: res.message,
              type: "success",
            });
          } else {
            this.$message({
              message: res.message,
              type: "error",
            });
          }
        });
    },

    // 弹出层确认按钮
    dialogSave() {
      this.$refs.dialogFormRef.validate((valid) => {
        if (!valid) {
          this.$message({
            message: "请修改正确的数据格式！",
            type: "warning",
          });
          return;
        } else {
          //验证通过调用保存接口
          if (this.dialogTitle == "修改包装") {
            let params = JSON.parse(JSON.stringify(this.dialogForm));
            params.response_datas = JSON.stringify(
              this.dialogForm.response_datas
            );
            this.getLogisticPutFinishedProductStorage(params);
          }
        }
      });
    },

    // 成品列表 删除
    getLogisticDeleteFinishedProductStorage(params) {
      baseManagement
        .getLogisticDeleteFinishedProductStorage(params)
        .then((res) => {
          console.log("成品列表 删除", res);
          if (res.code == 0) {
            this.getLogisticFinishedProductStorage(this.listQuery);
            this.$message({
              message: res.message,
              type: "success",
            });
          } else {
            this.$message({
              message: res.message,
              type: "error",
            });
          }
        });
    },
    // 点击删除当前行
    handleDelete(index, rowData) {
      console.log(rowData);
      this.$confirm("确定执行删除操作吗？", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      }).then(() => {
        let params = {
          prodect_code: rowData.pack_id,
          response_datas: JSON.stringify([
            { enter_storage_code: rowData.enter_storage_code },
          ]),
        };
        this.getLogisticDeleteFinishedProductStorage(params);
        this.dialogForm.response_datas.splice(index, 1);
      });
    },

    colDeleteBtn(rowData) {
      this.$confirm("确定执行删除操作吗？", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      }).then(() => {
        let params = {
          pack_id: rowData.pack_id,
          response_datas: JSON.stringify([]),
        };
        this.getLogisticDeleteFinishedProductStorage(params);
      });
    },
  },
  mounted() {
    // 人员内码和人员名称 查询
    this.getLogisticPersonCodeName();
    // 生产计划内码和名称接口： 查询
    this.getLogisticProductPlanCodeName();
    // 成品列表 查询
    this.getLogisticFinishedProductStorage(this.listQuery);
    // console.log(
    //   "userUrl/UserList  CurrentUserInfo",
    //   this.CurrentUserInfo
    // );
  },
};
</script>

